body {
  margin: 0;
  background: linear-gradient(0deg, rgba(33,33,33,1) 0%, rgba(22,22,22,1) 51%, rgba(3,28,33,1) 100%);background: linear-gradient(0deg, rgba(88,88,88,1) 0%, rgba(62,62,62,1) 50%, rgba(1,32,36,1) 100%);background: linear-gradient(0deg, rgba(13,13,13,1) 0%, rgba(8,8,8,1) 84%, rgba(1,32,36,1) 100%);
}

.ul_nav {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 15rem;
  background-color: #000000;
  position: fixed;
  height: 100%;
  /* overflow: auto; */
}

.nav-a a {
  display: block;
  color: rgb(145, 145, 145);
  padding: 8px 16px;
  text-decoration: none;
}

.nav-a a.active {
  background-color: #ffffff35;
  color: rgb(255, 255, 255);
}

.nav-a a:hover:not(.active) {
  background-color: #000000;
  color: white;
}


.background{
  width: 100%;


}